.header {
    position: fixed;
    top: 0;
    width: 100%;
    height: 64px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    background-color: rgb(56, 152, 212);
    color: white;
}

.chat-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: url("../assets/chat-background.png") repeat;
    background-size: contain;
}